.social {
  li {
    display: inline-block;
    @include notlast {
      margin-left: 79px;
      @include xl {
        margin-left: 65px; }
      @include lg {
        margin-left: 50px; }
      @include md {
        margin-left: 40px; }
      @include sm {
        margin-left: 30px; }
      @include r(374) {
        margin-left: 10px; } } }
  a {
    display: inline-block;
    @include tr(color .3s);
    @include hover {
      color: $accent; }
    .icon {
      font-size: 58px;
      @include xl {
        font-size: 50px; }
      @include lg {
        font-size: 40px; }
      @include md {
        font-size: 30px; }
      @include sm {
        font-size: 22px; } } } }
