.services {
  $this: &;
  $grid-gutter: 10px;

  @include flex(space-between, normal, row, wrap);
  @include xl {
    margin-left: -$grid-gutter / 2;
    margin-right: -$grid-gutter / 2;
    justify-content: center; }
  @include sm {
    display: block; }
  &__item {
    width: 197px;
    margin-bottom: 17px;
    @include xl {
      width: 20%;
      padding-left: $grid-gutter / 2;
      padding-right: $grid-gutter / 2; }
    @include lg {
      width: 33.33333%; }
    @include sm {
      width: percentage(185 / 375);
      margin-left: auto;
      margin-right: auto; }
    @include xs {
      width: 185px; } }

  &--full {
    $grid-gutter-sm: 18px;
    @include sm {
      display: flex;
      margin-left: -30px - ($grid-gutter-sm / 2);
      margin-right: -30px - ($grid-gutter-sm / 2); }
    #{$this}__item {
      @include sm {
        margin-left: 0;
        margin-right: 0;
        padding-left: $grid-gutter-sm / 2;
        padding-right: $grid-gutter-sm / 2;
        width: 50%; } } } }

.service-card {
  $this: &;
  padding: 39px 23px 23px;
  background-color: $white;
  @include flex(flex-start, normal, column);
  @include shadow;
  height: calc(100% - 16px);
  margin-bottom: 16px;
  text-align: center;
  &__img {
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 20px; }
  @include sm {
    flex-direction: row;
    padding: 9px;
    #{$this}__img {
      margin-bottom: 0;
      margin-right: 0;
      margin-left: 18px; } } }

.service-img {
  $this: &;
  width: 97px;
  padding-bottom: 50px;
  border-bottom: 2px solid  $blue;
  @include sm {
    &:not(#{$this}--side) {
      width: 69px;
      flex: 0 0 69px;
      padding-bottom: 0;
      padding-left: 18px;
      border-bottom: 0;
      border-left: 2px solid  $blue;
      @include flex(center, center); } }
  @include r(374) {
    &:not(#{$this}--side) {
      flex: 1 1 auto; } }

  &--side {
    @include lg {
      width: 60px;
      padding-bottom: 0;
      border-bottom: 0; }
    @include md {
      width: 50px; }
    @include sm {
      width: 37px; } } }

