.form {
  background-color: $white;
  padding: 54px 86px 36px;
  text-align: center;
  @include lg {
    padding: 40px 50px 25px; }
  @include md {
    padding: 30px 45px 18px; }
  @include sm {
    padding: 23px 38px 13px; }
  &__upttl {
    margin-bottom: 20px;
    @include sm {
      display: none; } }
  &__title {
    margin-bottom: 22px;
    @include sm {
      display: none; } }
  &__subttl {
    font-weight: 700;
    margin-bottom: 35px;
    @include md {
      margin-bottom: 25px; }
    @include sm {
      margin-bottom: 15px; } }
  &__fields {
    @include flex(flex-start, normal, row, wrap);
    margin-left: -10px;
    margin-right: -10px;
    margin-bottom: -67px + 37px;
    @include md {
      margin-bottom: 0; } }
  &__field {
    width: 50%;
    margin-bottom: 67px;
    padding-left: 10px;
    padding-right: 10px;
    @include md {
      margin-bottom: 40px; }
    @include sm {
      width: 100%;
      margin-bottom: 25px; } }
  &__btn {
    margin-bottom: 22px;
    @include md {
      margin-bottom: 15px; }
    @include sm {
      margin-bottom: 7px; } }
  &__code {
    font-weight: 700; } }
