.hero {
  $this: &;
  @include cover;
  background-color: $c-light;
  text-align: center;
  &:not(#{$this}--lg) {
    min-height: 259px;
    @include xl {
      min-height: 180px; }
    @include lg {
      min-height: 110px; }
    @include md {
      min-height: 90px; }
    @include sm {
      min-height: 73px; } }
  &__inner {
    @include flex(center, normal, column);
    padding-top: 30px;
    padding-bottom: 30px;
    // padding-top: 300px
    // padding-bottom: 148px
    // +xl
    //   padding-top: 250px
    //   padding-bottom: 110px
    // +lg
    //   padding-top: 200px
    //   padding-bottom: 80px
    // +md
    //   padding-top: 150px
    //   padding-bottom: 60px
    // +sm
    //   padding-top: 93px
 }    //   padding-bottom: 51px
  &__title {
    margin-bottom: 118px;
    @include xl {
      margin-bottom: 90px; }
    @include lg {
      margin-bottom: 70px; }
    @include md {
      margin-bottom: 55px; }
    @include sm {
      margin-bottom: 43px; } }

  &--lg {
    #{$this}__inner {
      min-height: 40vh;
      // +windowMinHeight($header-height)
      // +lg
 } } }      //   +windowMinHeight($header-height-lg)

