.input {
  width: 339px;
  @include lg {
    width: 100%; }
  #{$text-inputs} {
    width: 100%;
    background-color: transparent;
    border-bottom: 2px solid $black;
    height: 30px;
    @include tr(border-color .3s);
    @include placeholder {
      color: inherit; }
    &:focus {
      border-color: $accent; } } }
