.btn {
  display: inline-block;
  border: 1px solid $accent;
  background-color: $accent;
  padding: .45em 3.7em;
  font-weight: 700;
  text-align: center;
  @include tr(background-color .3s, color .3s, opacity .3s, border-color .3s);
  @include hover {
    background-color: $accent-2;
    border-color: $accent-2;
    color: $white; }
  @include sm {
    font-size: 16px; }
  &--lg {
    font-size: 44px; }
  &--full {
    width: 100%; } }
