.s-services {
  padding-top: 115px;
  padding-bottom: 159px;
  @include xl {
    padding-top: 140px;
    padding-bottom: 140px; }
  @include lg {
    padding-top: 80px;
    padding-bottom: 80px; }
  @include md {
    padding-top: 60px;
    padding-bottom: 60px; }
  @include sm {
    padding-top: 41px;
    padding-bottom: 41px; } }

.s-form {
  padding-top: 175px;
  padding-bottom: 175px;
  @include xl {
    padding-top: 140px;
    padding-bottom: 140px; }
  @include lg {
    padding-top: 80px;
    padding-bottom: 120px; }
  @include md {
    padding-top: 60px; }
  @include sm {
    padding-top: 47px; }
  &__form {
    @include lg {
      max-width: percentage(287 / 375);
      margin-left: auto;
      margin-right: auto; }
    @include sm {
      max-width: none;
      width: 287px; } } }


.s-descr {
  padding-top: 148px;
  padding-bottom: 220px;
  @include xl {
    padding-top: 120px;
    padding-bottom: 170px; }
  @include lg {
    padding-top: 100px;
 }    // padding-bottom: 140px
  @include md {
    padding-top: 70px;
    padding-bottom: 120px; }
  @include sm {
    padding-top: 42px;
    padding-bottom: 60px; }
  &__title {
    margin-bottom: 62px;
    @include xl {
      margin-bottom: 50px; }
    @include lg {
      margin-bottom: 35px; }
    @include md {
      margin-bottom: 25px; }
    @include sm {
      margin-bottom: 16px; } } }

.s-grid-items {
  position: relative;
  z-index: 2;
  padding-top: 297px;
  padding-bottom: 65px;
  @include xl {
    padding-top: 250px;
    padding-bottom: 50px; }
  @include lg {
    padding-top: 200px;
    padding-bottom: 40px; }
  @include md {
    padding-top: 150px;
    padding-bottom: 30px; }
  @include sm {
    padding-top: 94px;
    padding-bottom: 23px; }
  &__img {
    position: absolute;
    z-index: 1;
    top: 0;
    left: 50%;
    transform: translate(-50%, -50%);
    width: percentage(705 / 1920);
    @include sm {
      width: 207px; } }
  &__title {
    @include vcenter; } }

.s-form-service {
  padding-top: 60px;
  padding-bottom: 46px;
  @include xl {
    padding-top: 50px;
    padding-bottom: 45px; }
  @include lg {
    padding-top: 45px;
    padding-bottom: 45px; }
  @include sm {
    padding-top: 38px;
    padding-bottom: 38px;
    overflow: hidden; }
  &__form {
    margin-bottom: 99px;
    @include xl {
      margin-bottom: 70px; }
    @include lg {
      margin-bottom: 50px; }
    @include md {
      margin-bottom: 45px; }
    @include sm {
      margin-bottom: 37px; } } }

.s-blog {
  padding-top: 130px;
  padding-bottom: 149px; }

