.descr-items {
  $grid-gutter: 50px;
  $grid-gutter-lg: 30px;

  @include flex(flex-start, normal, row, wrap);
  margin-left: -$grid-gutter / 2;
  margin-right:  -$grid-gutter / 2;
  // margin-bottom: -$grid-gutter - 27px
  @include lg {
    margin-left: -$grid-gutter-lg / 2;
    margin-right:  -$grid-gutter-lg / 2;
 }    // margin-bottom: -$grid-gutter-lg - 27px
  &__item {
    width: 25%;
    padding-left: $grid-gutter / 2;
    padding-right: $grid-gutter / 2;
    margin-bottom: $grid-gutter + 27px;
    @include lg {
      padding-left: $grid-gutter-lg / 2;
      padding-right: $grid-gutter-lg / 2;
      margin-bottom: $grid-gutter-lg+ 27px; }
    @include sm {
      width: 50%; } } }

.descr-item {
  color: $accent-2;
  text-align: center; }
