html {
  font-size: $fz;
  line-height: 1.3;
  @include md {
    font-size: $fz-md-px; }
  @include sm {
    font-size: $fz-sm-px; } }

body {
  background-color: $white;
  min-width: 320px;
  @include reg;
  color: $c-text;
  @include no-scroll; }


.icon {
  display: inline-block;
  fill: currentColor;
  font-size: inherit; }

.lazy {
  $this: &;
  &--loaded:not(#{$this}--no-fade-in) {
    animation: fadeIn 1s; } }

.container {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: $container-padding;
  padding-right: $container-padding;
  @include xxlmin {
    max-width: $container-width; }
  @include xlmin {
      max-width: $container-width-xl; }
  @include lgmin {
      max-width: $container-width-lg; }
  @include mdmin {
      max-width: $container-width-md; }
  &--lg {
    @include xxlmin {
      max-width: $container-lg-width; }
    @include xlmin {
      max-width: $container-lg-width-xl; }
    @include lgmin {
      max-width: $container-lg-width-lg; } }
  &--xl {
    max-width: $container-xl-width; } }


.out {
  padding-top: $header-height;
  @include lg {
    padding-top: $header-height-lg; } }

.descr {
  p {
    @include notlast {
      margin-bottom: 30px; } } }

