.section {
  $this: &;
  &__title {
    text-align: center;
    margin-bottom: 24px;
    @include lg {
      margin-bottom: 15px; }
    @include sm {
      margin-bottom: 9px; } }
  &__subttl {
    max-width: 752px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 100px;
    @include xl {
      margin-bottom: 80px; }
    @include lg {
      margin-bottom: 50px; }
    @include md {
      margin-bottom: 35px; }
    @include sm {
      margin-bottom: 24px; } }
  &__text {
    p {
      @include notlast {
        margin-bottom: 15px; } } }
  &__side-wrap {
    @include flex(flex-start, normal, row, wrap); }
  &__side-content {
    $gutter: 39px;
    width: percentage(1108 / 1661);
    padding-left: $gutter / 2; }
  &__side-aside {
    $gutter: 39px;
    width: percentage((1661 - 1108) / 1661);
    padding-right: $gutter / 2; }
  &__aside {
    position: sticky;
    top: $header-height; }
  &__post {
    margin-bottom: 30px; }



  &--bg {
    @include cover;
    background-color: $c-light; }
  &--icon {
    min-height: 900px;
    padding-top: 80px;
    padding-bottom: 80px;
    position: relative;
    @include xl {
      padding-top: 60px;
      padding-bottom: 60px;
      min-height: 800px; }
    @include lg {
      padding-top: 50px;
      padding-bottom: 50px;
      min-height: 600px; }
    @include md {
      padding-top: 40px;
      padding-bottom: 40px;
      min-height: 400px; }
    @include sm {
      padding-top: 27px;
      padding-bottom: 38px; }
    #{$this}__title {
      padding-left: 60px;
      padding-right: 60px;
      @include mdmin {
        text-align: right;
        padding-left: 0;
        padding-right: 0; }
      @include md {
        margin-bottom: 30px; } }
    #{$this}__right-icon-wrap {
      margin-left: auto;
      margin-right: auto;
      width: 100%;
      position: relative;
      z-index: 1;
      top: 1em;
      @include xxlmin {
        max-width: $container-lg-width - ($container-padding*2); }
      @include xlmin {
        max-width: $container-lg-width-xl - ($container-padding*2); }
      @include lgmin {
        max-width: $container-lg-width-lg - ($container-padding*2); }
      @include lg {
        top: 0; } }
    #{$this}__right-icon {
      position: absolute;
      left: 100%;
      z-index: 1;
      top: 0;
      @include lg {
        left: auto;
        right: 10px; } } } }
